header {
    height: 100vh;
    padding-top: 3rem;
    overflow: hidden;
}

.header_container {
    text-align: center;
    position: relative;
    height: 100%;
}

a {
    margin-top: 2.5rem;
    display: flex;
    justify-content: center;
}

.header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    margin-left: 10rem;
    font-size: 1.5rem;
    justify-content: center;
}

.header_socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 25rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.me img {
    margin-top: -3rem;
    margin-bottom: 2rem;
}

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
    margin-right: 10rem;
    margin-top: 20rem;
}

@media screen and (max-width: 1024px) {
    header {
        height: 60vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header_socials, .scroll_down {
        display: none;
    }
}